(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .factory('PolicyFactory', fctry);

  /** @ngInject */
  function fctry($http,
                 $q,
                 $timeout,
                 ACCIDENT_TYPE,
                 TRAVEL_TYPE) {
    var vm = this;

    var headers = {
      'SecretKey': '762f5692-5a8b-4f8e-a09e-a27a277c0c56',
      'x-tenant-name': 'coherent'
    };

    vm.opts = {};

    vm.opts[ACCIDENT_TYPE] = {
      name: 'policy_personal_accident',
      claimTitle: 'claim_accident_details',
      img: 'assets/images/accident/icon.svg',
      color: 'purple'
    };

    vm.opts[TRAVEL_TYPE] = {
      name: 'policy_travel',
      img: 'assets/images/travel/icon.svg',
      color: 'purple'
    };

    vm.hospitalStatuses = [{
      name: 'claim_out_of_hospital',
      value: 'Out_Pa'
    },{
      name: 'claim_in_hospital',
      value: 'In_Pa'
    },{
      name: 'claim_recovering',
      value: 'Recover'
    }];

    vm.confirmationMessages = {
      editSuccess: 'policy_your_details_have_been_updated',
      cancelSuccess: 'policy_your_policy_will_expire_on',
      claimSuccess: 'claim_your_claim_has_been_submitted'
    };

    vm.cancellationReasons = [
      'policy_cannot_commit_to_monthy_payments',
      'policy_change_of_plans',
      'policy_other'
    ];

    vm.withdrawalMethods = [{
      name: 'claim_bank_transfer',
      value: 'bank_transfer'
    },{
      name: 'claim_credit_bank',
      value: 'credit_bank'
    },{
      name: 'claim_credit_union',
      value: 'credit_union'
    },{
      name: 'claim_ewallet',
      value: 'ewallet'
    }];

    vm.errorCodes = {
      C1: 'Insured name equal to injury person name',
      C2: 'Check Accident occurred within the cover period',
      C3: 'Validate accident can be classified as traffic accidents',
      C4: 'Check Hospitalisation start date within 180 days after accident',
      C5: 'Flag up if treatment not completed and potential further payments',
      C6: 'Check surgery type covered within the policy term',
      C7: 'Check surgery date within 180 days after accident',
      C8: 'If surgery operate outside hospitalisation period',
      C9: 'Confirm third party claimants not live with policyholder',
      C10: 'Third Party liability amount exceed limit'
    };

    vm.get = function (policyNumber) {
      return $http({
        method: 'GET',
        url: 'https://general.sonic.dev.coherent.com.hk/general/v2/enquiry/policy/' + policyNumber
      });
    };

    vm.edit = function (data) {
      return $http({
        method: 'POST',
        url: 'https://general.sonic.dev.coherent.com.hk/general',
        data: data
      });
    };

    vm.cancel = function (data) {
      return $http({
        method: 'POST',
        url: 'https://general.sonic.dev.coherent.com.hk/general/v2/service/cancelpolicy',
        data: data
      });
    };

    vm.getRefundPrice = function (type, data) {
      return $http({
        method: 'POST',
        url: 'https://excel.sonic.dev.coherent.com.hk/api/v1/product/coherent/engines/Execute/' + type,
        headers: headers,
        data: data
      });
    };

    vm.validate = function (type, data) {
      return $http({
        method: 'POST',
        url: 'https://excel.sonic.dev.coherent.com.hk/api/v1/product/coherent/engines/Execute/' + type,
        headers: headers,
        data: data
      });
    };

    vm.claim = function (data) {
      return $http({
        method: 'POST',
        url: 'https://general.sonic.dev.coherent.com.hk/general/v2/claim/initiate',
        data: data
      });
    };

    return vm;
  }
})();

